  listCategory: any = {};
  lstCategory: any = [];
  listProduct: any[] = [];
  listProductObject: any = {};
  listPhanLoaiProduct: any = {};
  listTrademark: any[] = [];
  listSubCategory: any[] = [];
  listSubCategoryObject: any = {};
  listQuyCach: any[] = [];
  listEvent: any[] = [];
  listUserChat: any[] = [];
  columnsTableProduct = [];
  EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
  EXCEL_EXTENSION = '.xlsx';
  chatStatus = false;
  listChatContent: any = {};
