  getListDiscount() {
    let listDiscount = this.db.list("Events/");
    listDiscount.valueChanges().subscribe(discount => {
      this.listProductDiscount = {};
      this.listCodediscount = {};
      this.listEvent = [];
      for (const key in discount) {
        const element: any = discount[key];
        if (element.id) {
          this.listEvent.push(element);
        }
        if (element.Code) {
          for (const keyCode in element.Code) {
            const elementCode = element.Code[keyCode];
            this.listCodediscount[elementCode.code] = { id: elementCode.code, name: element.title };
          }
        }
        if (element.Product) {
          for (const keyP in element.Product) {
            const elementP = element.Product[keyP];
            this.listProductDiscount[elementP.id] = { id: elementP.id, name: element.title };
          }
        }
      }
    });
  }
  getListProduct() {
    let listPro = this.db.list("Product/");
    listPro.valueChanges().subscribe(product => {
      this.listProduct = [];
      let listCate = 0;
      let listSet = 0;
      this.listPhanLoaiProduct = {};
      this.listProductObject = {};
      if (this.listCategory) {
        if (product) {
          for (let index = 0; index < product.length; index++) {
            const element: any = product[index];
            let cateCount = 0;
            let pro = {};
            this.listPhanLoaiProduct[element["Phân loại"]] = element;
            this.listProductObject[element["id"]] = element;
            for (const key in element) {
              if (key.includes("Giá set")) {
                let splitString = key.split("_");
                if (splitString.length > 1) {
                  if (listSet < parseInt(splitString[1]) + 1) {
                    listSet = parseInt(splitString[1]) + 1;
                  }
                }
                if (listSet == 0) {
                  listSet = 1;
                }
              }
              if (parseInt(key).toString() != "NaN") {
                pro["Tên category" + cateCount] = this.listCategory[element[key]].title;
                pro["ID category" + cateCount] = this.listCategory[element[key]].id;
                cateCount++;
              }
              else {
                pro[key] = element[key];
              }
            }
            this.listProduct.push(pro);
            if (cateCount > listCate) {
              listCate = cateCount;
            }
          }
          this.createTableProductColumns(listCate, listSet);
        }
      }
    });
  }
  getListCategory() {
    let listCate = this.db.list("Category/");
    listCate.valueChanges().subscribe(category => {
      this.listCategory = {};
      this.lstCategory = [];
      if (category) {
        for (let index = 0; index < category.length; index++) {
          const element: any = category[index];
          this.listCategory[element.title] = { id: element.id, value: element.value, title: element.title, active: element.active };
          this.listCategory[element.id] = { id: element.id, value: element.value, title: element.title, active: element.active };
          this.lstCategory.push({ id: element.id, value: element.value, title: element.title, active: element.active });
          if (element.listMenu) {
            for (const keySub1 in element.listMenu) {
              const elementSub1 = element.listMenu[keySub1];
              this.listCategory[elementSub1.title] = { id: elementSub1.id, value: elementSub1.value, title: elementSub1.title, active: elementSub1.active };
              this.listCategory[elementSub1.id] = { id: elementSub1.id, value: elementSub1.value, title: elementSub1.title, active: elementSub1.active };
              this.lstCategory.push({ id: elementSub1.id, value: elementSub1.value, title: elementSub1.title, active: elementSub1.active });
              if (elementSub1.listMenu) {
                for (const keySub2 in elementSub1.listMenu) {
                  const elementSub2 = elementSub1.listMenu[keySub2];
                  this.listCategory[elementSub2.title] = { id: elementSub2.id, value: elementSub2.value, title: elementSub2.title, active: elementSub2.active };
                  this.listCategory[elementSub2.id] = { id: elementSub2.id, value: elementSub2.value, title: elementSub2.title, active: elementSub2.active };
                  this.lstCategory.push({ id: elementSub2.id, value: elementSub2.value, title: elementSub2.title, active: elementSub2.active });
                }
              }
            }
          }
        }
      }
    });
  }
  settingTableHeight(height: any[]) {
    let element = 0;
    let contentSize = 0;
    if (height.length >= 1) {
      if (height[0].el) {
        contentSize = height[0].el.offsetHeight;
      }
      if (height.length == 2) {
        for (let index = 0; index < height[1].length; index++) {
          let minusElement = height[1][index];
          if (minusElement.el) {
            element = element - minusElement.el.offsetHeight;
          }
        }
      }
      return (element + contentSize) + "px";
    }
  }
