  settingSizeDevice() {
    if (window.innerWidth > 640) {
      this.appValueService.APP_PLATFORM_DESKTOP = true;
    }
    else {
      this.appValueService.APP_PLATFORM_DESKTOP = false;
    }
    if (window.innerWidth <= 640) {
      this.appValueService.APP_PLATFORM_MOBILEWEB = true;
    }
    else {
      this.appValueService.APP_PLATFORM_MOBILEWEB = false;
    }
    if (this.platform.is("mobile")) {
      this.appValueService.APP_PLATFORM_MOBILE = true;
    }
    else {
      this.appValueService.APP_PLATFORM_MOBILE = false;
    }
    if (this.platform.is("ios")) {
      this.appValueService.APP_PLATFORM_IOS = true;
    }
    else {
      this.appValueService.APP_PLATFORM_IOS = false;
    }
    if (this.platform.is("android")) {
      this.appValueService.APP_PLATFORM_ANDROID = true;
    }
    else {
      this.appValueService.APP_PLATFORM_ANDROID = false;
    }
    if (this.appValueService.APP_TABULATOR) {
      let height = this.settingTableHeight(this.appValueService.APP_TABULATOR_HEIGHT);
      this.appValueService.APP_TABULATOR.setHeight(height);
    }
  }
