  getJson(file, sheetName, cb) {
    let reader = new FileReader();
    let result;
    reader.onload = (e: any) => {
      let data = e.target.result;
      let workbook = XLSX.read(data, { type: 'binary' });
      let first_sheet_name = sheetName;
      let dataObjects = XLSX.utils.sheet_to_json(workbook.Sheets[first_sheet_name]);
      if (dataObjects.length > 0) {
        result = dataObjects;
        cb(result);
      } else {
        this.showError("Không có data");
      }
    }

    reader.onerror = (ex) => {
      this.showError(ex);
    }
    reader.readAsBinaryString(file);
    return result;
  }
