  async presentAlertConfirmDelete(id, header, message, mode) {
    const alert = await this.alertCtrl.create({
      header: header,
      message: message,
      buttons: [
        {
          text: 'Hủy',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
          }
        }, {
          text: 'Xóa',
          handler: () => {
            this.deleteEvent(id, mode);
          }
        }
      ]
    });
    await alert.present();
  }
  deleteEvent(id, mode) {
    this.loadingStart();
    let dataInfo = {};
    let addUpdateData = this.db.object("/");
    if (mode == "News") {
      dataInfo[`News/${"" + id}/active`] = false;
      addUpdateData.update(dataInfo).then((result) => {
        this.loadingStop();
      })
        .catch(err => {
          this.showError(err);
        });
    }
    else if (mode == "Product") {
      dataInfo[`Product/${"" + id}/active`] = false;
      addUpdateData.update(dataInfo).then((result) => {
        this.loadingStop();
      })
        .catch(err => {
          this.showError(err);
        });
    }
    else if (mode == "ProductDetails") {
      dataInfo[`Kho/${"" + id}`] = null;
      addUpdateData.update(dataInfo).then((result) => {
        this.loadingStop();
      })
        .catch(err => {
          this.showError(err);
        });
    }
    else if (mode == "Events") {
      dataInfo[`Events/${"" + id.id}`] = null;
      addUpdateData.update(dataInfo).then((result) => {
        this.loadingStop();
      })
        .catch(err => {
          this.showError(err);
        });
    }
    else if (mode == "Daily") {
      dataInfo[`Daily/${"" + id}`] = null;
      addUpdateData.update(dataInfo).then((result) => {
        this.loadingStop();
      })
        .catch(err => {
          this.showError(err);
        });
    }
  }
  createChatDataInsert(data, currentSupporter) {
    data.support = currentSupporter.support.id;
    data.time = this.generateHHmmHyPhen(new Date());
    let dataInfo = {};
    let time = new Date().getTime();
    if (currentSupporter.support.id) {
      for (const key in data) {
        const element = data[key]
        dataInfo[`Users/${currentSupporter.id}/chats/currents/${currentSupporter.support.id}/${key}/`] = element;
        dataInfo[`Users/${currentSupporter.id}/chats/old/${data.last}/${currentSupporter.support.id}/${time}/${key}/`] = element;
      }
      dataInfo[`supporter/${currentSupporter.support.id}/users/${currentSupporter.id}/read`] = true;
      let addNewData = this.db.object("/");
      addNewData.update(dataInfo).then(() => {
      }).catch(err => {
        this.writeLog(err);
        this.showError("Có lỗi xảy ra.");
      });
    }
  }
  deleteChat(chatMember) {
    if (chatMember.read == true) {
      let dataInfo: any = {};
      dataInfo[`supporter/${this.appValueService.APP_AUTHENTICATION.id}/users/${chatMember.id}`] = null;
      let addNewData = this.db.object("/");
      addNewData.update(dataInfo).then(() => {
      }).catch(err => {
        this.writeLog(err);
        this.showError("Có lỗi xảy ra.");
      });
    }
  }
  writeLog(err) {
    let dataInfo = {};
    let key = this.generateNewKey();
    if (this.appValueService.APP_AUTHENTICATION.id) {
      dataInfo[`Logs/${key}/user/`] = this.appValueService.APP_AUTHENTICATION.id;
    }
    else {
      dataInfo[`Logs/${key}/user/`] = "Anonymous user";
    }
    dataInfo[`Logs/${key}/error/`] = err + "";
    dataInfo[`Logs/${key}/createDate/`] = this.generateDDMMYYYY(new Date());
    let addNewData = this.db.object("/");
    addNewData.update(dataInfo).then(() => {
    }).catch(err => {
      this.showError("Hệ thống đang bảo trì.");
    });
  }
  generateDDMMYYYY(value: Date) {
    return value.getDate() + "/" + (value.getMonth() + 1) + "/" + value.getFullYear();
  }
  generateDDMMYYYYHyPhen(value: Date) {
    return value.getDate() + "-" + (value.getMonth() + 1) + "-" + value.getFullYear();
  }
  generateHHmmHyPhen(value: Date) {
    return value.getHours() + ":" + value.getMinutes();
  }
  removeInvalidChars(text: string) {
    text = text.replace(/[.$#\/\[\]]/g, '');
    return text;
  }
  generateNewKey(): string {
    return this.db.list("/").push(undefined).key;
  }
  generateDateDDMMYYY(value: Date) {
    return value.getDate() + "/" + (value.getMonth() + 1) + "/" + value.getFullYear();
  }
  generateDateDDMMYYYHyphen(value: Date) {
    return value.getDate() + "-" + (value.getMonth() + 1) + "-" + value.getFullYear();
  }
  exportAsExcelFile(json: any[], excelFileName: string, sheetName: string): void {
    const worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(json);
    let sheet = {};
    sheet[sheetName + ""] = worksheet;
    const workbook: XLSX.WorkBook = { Sheets: sheet, SheetNames: [sheetName] };
    const excelBuffer: any = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
    this.saveAsExcelFile(excelBuffer, excelFileName);
  }
  private saveAsExcelFile(buffer: any, fileName: string): void {
    const data: Blob = new Blob([buffer], { type: this.EXCEL_TYPE });
    FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + this.EXCEL_EXTENSION);
  }
  getDateFromString(date, formatIn) {
    return moment(date, formatIn).toDate();
  }
  getDateFromStringDD(date, formatIn) {
    return moment(date, formatIn).format("D/M/YYYY");
  }
