import { Component, HostListener, ViewChild } from '@angular/core';
import { Platform, MenuController, NavController } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { Router, RouterEvent } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { UtilsService } from './services/utils.service';
import { AlertController } from '@ionic/angular';
import { AppValueService } from './services/app-value.service';
import { ConstantsService } from './services/constants.service';
import { Storage } from '@ionic/storage';
@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html'
})
export class AppComponent {
  selectedPath = '';
  pages = [
    {
      title: 'Quản lí Kho',
      url: this.constantsService.PATH_KHO_MANAGEMENT
    },
    {
      title: 'Quản lí sản phẩm',
      url: this.constantsService.PATH_PRODUCT_MANAGEMENT
    },
    {
      title: 'Quản lí Category',
      url: this.constantsService.PATH_CATEGORY_MANAGEMENT
    },
    {
      title: 'Quản lí event',
      url: this.constantsService.PATH_EVENT_MANAGEMENT
    },
    {
      title: 'Daily free gift',
      url: this.constantsService.PATH_DAILYFREEGIFT_MANAGEMENT
    },
    {
      title: 'Tin tức',
      url: this.constantsService.PATH_NEWS_MANAGEMENT
    },
    {
      title: 'Tài khoản',
      url: this.constantsService.PATH_USER_MANAGEMENT
    },
    {
      title: 'Bài test da',
      url: this.constantsService.PATH_TEST_MANAGEMENT
    },
    {
      title: 'Thông báo',
      url: this.constantsService.PATH_NOTIFY_MANAGEMENT
    },
    {
      title: 'Đơn hàng',
      url: this.constantsService.PATH_ORDER_MANAGEMENT
    },
    {
      title: 'Thống kê',
      url: this.constantsService.PATH_SUMMARY_MANAGEMENT
    },
    {
      title: 'Chat',
      url: this.constantsService.PATH_CHAT_MANAGEMENT
    }
  ];
  idDrag = false;
  constructor(private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private router: Router,
    private translate: TranslateService,
    public utilsService: UtilsService,
    public appValueService: AppValueService,
    public constantsService: ConstantsService,
    private alertController: AlertController,
    private storage: Storage,
    private navCtrl: NavController,
    private menuCtr: MenuController) {
    this.initializeApp();
    this.router.events.subscribe((event: RouterEvent) => {
      if (event && event.url) {
        this.selectedPath = event.url;
      }
    });
    this.initTranslate();
    this.utilsService.settingAppConfig();
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }
  private initTranslate() {
    this.storage.get('APP_AUTHENTICATION').then((value) => {
      if (value) {
        this.utilsService.appValueService.APP_AUTHENTICATION = value;
      }
    });
  }
  checkDrag() {
    this.idDrag = true;
  }
  chatShow() {    
    if (this.idDrag == false) {
      this.utilsService.isChatOpen = !this.utilsService.isChatOpen;
      if(this.utilsService.isChatOpen){
        this.navCtrl.navigateRoot('/chat-management');
      }
      else {
        this.navCtrl.back();
      }      
    }
    this.idDrag = false;
  }
  logout() {
    this.menuCtr.close();
    this.storage.remove('APP_AUTHENTICATION');
    this.router.navigateByUrl(this.constantsService.PATH_LOGIN);
  }
  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.utilsService.settingSizeDevice();
  }

}
